    margin-bottom: 28px;
    text-align: left; }
  .request fieldset {
    margin-bottom: 11px; }
  .request button {
    display: block;
    margin: 0 auto; }
  .request .request__invalid-input .request__error-message {
    bottom: -15px; }
  .request__form-row {
    flex-direction: column; }
  .request__form-group {
    width: auto;
    margin-bottom: 20px; }
  .request__checkbox-container {
    flex-direction: column;
    margin-bottom: -3px; }
    .request__checkbox-container fieldset {
      width: auto;
      margin-bottom: 0; }
      .request__checkbox-container fieldset:nth-child(2) {
        margin-top: 0; }
  .request__agreement {
    margin-bottom: 28px; }
  .request__footnote {
    margin-top: 0;
    margin-bottom: 28px;
    padding-top: 11px; } }
